html,
body {
  font-family: "Azeret Mono", "Noto Sans TC", monospace, sans-serif;
}

a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
}

a:hover {
  color: #62db54;
}

img {
  max-width: 100%;
  height: auto;
}

h1, .h1 {
  font-size: 24px;
}

@media (min-width: 768px) {
  h1, .h1 {
    font-size: 48px;
  }
}

main {
  min-height: calc(100vh - 58px - 72px);
}

@media (min-width: 768px) {
  main {
    min-height: calc(100vh - 69.9px - 72px);
  }
}

@media (min-width: 992px) {
  main {
    min-height: calc(100vh - 66px - 72px);
  }
}

@media (max-width: 395px) {
  main {
    min-height: calc(100vh - 58px - 96px);
  }
}

.font-Noto {
  font-family: "Noto Sans TC", sans-serif;
}

.btn:focus,
.navbar-toggler:focus {
  box-shadow: none;
}

.btn-gradient {
  background: linear-gradient(to right, #09abf6, #62db54);
  border: none;
  color: #fff;
}

.border-gradient {
  width: 100%;
  height: 2px;
  background: linear-gradient(to left, #09abf6 50%, #62db54 50%);
}

.text-gradient {
  background: linear-gradient(#09abf6, #62db54);
  background-clip: text;
  color: transparent;
}

.navbar-brand {
  width: 24px;
}

@media (min-width: 768px) {
  .navbar-brand {
    width: 174.42px;
  }
  .navbar-brand img {
    content: url("../images/logo.svg");
  }
}

.nav-link {
  color: #fff;
}

nav .border-gradient {
  position: fixed;
  top: 58px;
}

@media (min-width: 576px) {
  nav .border-gradient {
    top: 65px;
  }
