.cube {
  position: absolute;
  width: 10px;
  height: 10px;
}

.cube__lt {
  left: calc(-10px / 2);
  top: calc(-10px / 2);
}

.cube__rt {
  right: calc(-10px / 2);
  top: calc(-10px / 2);
}

.cube__lb {
  left: calc(-10px / 2);
  bottom: calc(-10px / 2);
}

.cube__rb {
  right: calc(-10px / 2);
  bottom: calc(-10px / 2);
}

